@import './variables';

@mixin aspect-ratio($width, $height) {
  overflow: hidden;
  position: relative;
  transition: height 0.3s ease-in-out, width 0.3s ease-in-out;
  &:before {
    content: "";
    width: 1px;
    margin-left: -1px;
    float: left;
    height: 0;
    padding-top: ($height / $width) * 100%;
  }
  &:after {
    clear: both;
    content: '';
    display: table;
  }
}

.ratio-20-9 {
  @include aspect-ratio(20, 9);
}

.ratio-3-2 {
  @include aspect-ratio(3, 2.25); // supposed to be 3/2, additional padding messes it up I think
}

.ratio-4-3 {
  @include aspect-ratio(4, 3);
}

.ratio-3-4 {
  @include aspect-ratio(3, 4);
}

.ratio-image-slide {
  @include aspect-ratio(710, 474);
}

.ratio-square {
  @include aspect-ratio(1, 1);
}

.ratio-flip-card {
  @include aspect-ratio(3, 2);

  @media screen and (min-width: $md-break) {
    @include aspect-ratio(3, 1);
  }

  @media screen and (min-width: $lg-break) {
    @include aspect-ratio(3, 4);
  }
}

.ratio-5-4-card {
  @include aspect-ratio(3, 2);

  @media screen and (min-width: $md-break) {
    @include aspect-ratio(5, 4);
  }
}


