
.progress-selector-indicator:hover + .progress-selector-content .arrow-container .bracket-left {
  -webkit-transform: translateX(-3px);
          transform: translateX(-3px);
}
.progress-selector-indicator:hover + .progress-selector-content .arrow-container .bracket-right {
  -webkit-transform: translateX(3px);
          transform: translateX(3px);
}



