@import './variables';

// set grid size
$grid-size: 16;

// mixins for width and margin calculation
@mixin width($width) {
  width: ($width / $grid-size) * 100%;
}

@mixin margin-left($width) {
  margin-left: ($width / $grid-size) * 100%;
}

.row {
  display: flex;
  flex-wrap: wrap;

  &.align-top {
    align-items: flex-start;
  }

  &.align-bottom {
    align-items: flex-end;
  }

  &.align-center {
    align-items: center;
  }

  &.flush {
    padding: 0;

    & > [class^="span-"]:not([class^="offset-"]) {
      padding: 0;
    }
  }

  &.justify-around {
    justify-content: space-around;
  }

  &.justify-between {
    justify-content: space-between;
  }
}

// base class
[class^="span-"] {
  width: 100%;
  box-sizing: border-box;

  &.align-top {
      align-self: flex-start;
  }

  &.align-bottom {
      align-self: flex-end;
  }

  &.align-center {
      align-self: center;
  }

  > .row {
      margin-left: -12px;
      margin-right: -12px;
  }
}

.span-auto {
  width: auto;
}

// generate classes based on grid variable
@for $i from 1 through $grid-size {

  @media screen and (max-width: $xs-max) {
    // base column sizes
    .span-#{$i}-xs {
      @include width($i);
    }
    // base offsets
    .offset-#{$i}-xs {
      @include margin-left($i);
    }
  }


  // sm
  @media screen and (min-width: $sm-break) and (max-width: $sm-max) {
    .span-#{$i}-sm {
      @include width($i);
    }
    .offset-#{$i}-sm {
      @include margin-left($i);
    }
  }

  // md
  @media screen and (min-width: $md-break) and (max-width: $md-max) {
    .span-#{$i}-md {
      @include width($i);
    }
    .offset-#{$i}-md {
      @include margin-left($i);
    }
  }

  // lg
  @media screen and (min-width: $lg-break) {
    .span-#{$i}-lg {
      @include width($i);
    }
    .offset-#{$i}-lg {
      @include margin-left($i);
    }
  }
}


