@import './variables';

@font-face {
  font-family: 'Gotham-Light';
  src: url('https://s3-us-west-2.amazonaws.com/fwov-vpv/Gotham-Light.otf') format('opentype');
}

@font-face {
  font-family: 'Gotham-Medium';
  src: url('https://s3-us-west-2.amazonaws.com/fwov-vpv/Gotham-Medium.otf') format('opentype');
}

@font-face {
  font-family: 'Gotham-Book';
  src: url('https://s3-us-west-2.amazonaws.com/fwov-vpv/Gotham-Book.otf') format('opentype');
}

@font-face {
  font-family: 'Gotham-Black';
  src: url('https://s3-us-west-2.amazonaws.com/fwov-vpv/Gotham-Black.otf') format('opentype');
}

h1, .h1 {
  font-family: 'Gotham-Light';
  font-size: 36px;
  color: $black;
  letter-spacing: 2px;
  line-height: 48px;
  font-weight: 100;
  @media screen and (max-width: $sm-break) {
    font-size: 24px;
    letter-spacing: 1px;
    line-height: 30px;
  }
  &.title {
    font-family: 'Gotham-Black';
    font-size: 72px;
    color: $black;
    letter-spacing: 4px;
    line-height: 72px;
  }
}

h2, .h2 {
  font-family: 'Gotham-Light';
  font-size: 28px;
  color: $black;
  letter-spacing: 1px;
  line-height: 36px;
}

p, .p {
  font-family: 'Gotham-Book';
  font-size: 16px;
  color: $black;
  letter-spacing: 0.8px;
  line-height: 24px;
  &.large {
    font-family: 'Gotham-Light';
    font-size: 30px;
    color: $black;
    letter-spacing: 0;
    line-height: 38px; //44px;
    @media screen and (max-width: $sm-break) {
      font-size: 24px;
      line-height: 36px;
    }
  }
}

.subheader, p.subheader, .p.subheader {
  font-family: 'Gotham-Medium';
  font-size: 16px;
  color: $black;
  letter-spacing: 0.8px;
}


