@import './variables';

body {
  font-family: 'Gotham-Book', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  user-select: none;
  margin: 0px;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #FEF3F3, #E4EBF1);
  background-size: cover;
}

.bg-gradient {
  background: linear-gradient(45deg, #FEF3F3, #E4EBF1);
}

.debug {
  background: $debug;
}

.relative {
  position: relative;
}

.auto {
  margin: auto;
}

.invisible {
  opacity: 0;
}

.upcase {
  text-transform: uppercase;
}

.bold {
  font-weight: bold;
}

// z-indexes
@for $i from 1 through 9 {
  .ind-#{$i} {
    z-index: $i;
  }
}

// top
.ind-top {
  z-index: 9999;
}

.overflow-visible {
  overflow: visible;
}

.overflow-hidden {
  overflow: hidden;
}

.border-blue {
  background: $white;
  border: 1px solid $blue;
}

.border-red {
  background: $white;
  border: 1px solid $red;
}

.border-green {
  background: $white;
  border: 1px solid $green;
}

.fill {
  &.green {
    fill: $green;
  }
  &.red {
    fill: $red;
  }
}

  //- col with rollover only -//

  .border-bg-rollover {
    background: $white;
    border: 1px solid transparent;
    cursor: pointer;
    opacity: 0.75;
    transition: all 0.75s;
    // hide crosshairs if injected
    + .crosshair-container {
      opacity: 0;
    }
    &:hover {
      opacity: 1;
      border: 1px solid $blue;
      + .crosshair-container {
        opacity: 1;
      }
    }
  }
  
  .show-sm {
    @media screen and (min-width: $md-break) {
      * {
        display: none;
      }
    }
  }

  .hide-sm {
    @media screen and (max-width: $sm-max) {
      display: none;
    }
  }

  .extra-padding-bottom {
    padding-bottom: 144px;
  }
  
  // gutters

  .gutter-horizontal {
    position: relative;
    margin: auto;
    max-width: calc(100% - 80px - 80px);
    @media screen and (max-width: $sm-max) {
      max-width: 100%;
    }
  }

    // todo: vertical align rows center instead of vertical padding
    // .gutter-vertical {
    //   padding-top: calc(24px + 36px);
    //   padding-bottom: calc(96px + 24px + 36px);
    //   @media screen and (max-width: $sm-max) {
    //     padding-top: 0px;
    //     padding-bottom: 60px;
    //   }
    // }

    .max-height-vertical {
      position: relative;
      margin-top: 48px;
      width: 100%;
      height: calc(100vh - 96px);
      max-height: calc(100vh - 96px);
      @media screen and (max-width: $md-max) {
        height: auto;
        max-height: 100%;
        margin-bottom: calc(96px + 48px);
      }
      @media screen and (max-width: $sm-max) {
        margin-bottom: calc(60px + 48px);
      }
    }

    .test-overflow {
      box-sizing: border-box;
      border: 5px solid $debug;
      overflow-x: hidden;
      overflow-y: auto;
      &.overflow-hidden {
        overflow-y: hidden;
      }
      @media screen and (max-width: $md-max) {
        overflow-x: hidden;
        overflow-y: hidden;
      }
    }

    .max-100 {
      max-height: 100%;
    }

    .max-98 {
      max-height: 98px;
    }

    .border-box {
      box-sizing: border-box;
    }


